import '../css/reset.css';
import '../css/style.css';
import Pencil from './tools/Pencil';
import colorGrab from './tools/ColorGrab';
import Color from './Color';
import activateTool from './tools/utility/activateTool';
import changeCanvasSize from './changeCanvasSize';
import hexToRGBA from './tools/utility/hexToRGBA';
import Eraser from './tools/Eraser';
import Stroke from './tools/Stroke';
import Fill from './tools/Fill';
import Frame from './Frames/Frame';
import FrameTools from './Frames/FrameTools';
import LocalStorageUtility from './tools/utility/localStorageUtility';
import FrameData from './Frames/FrameData';
import activateFrame from './Frames/activateFrame';
import drawImage from './tools/utility/drawImage';

const canvas = document.querySelector('.canvas');
const layer = document.querySelector('.canvas_layer');
const bucket = document.querySelector('.action--bucket');
const grabColor = document.querySelector('.action--grab_color');
const pencil = document.querySelector('.action--pencil');
const stroke = document.querySelector('.action--stroke');
const eraser = document.querySelector('.action--eraser');
const currentColor = document.querySelector('.color--input-current');
const prev = document.querySelector('.color--input-prev');
const sizeSwitcher = document.querySelector('.size_switcher');
const addFrameButton = document.querySelector('.add_frame_button');
const frameList = document.querySelector('.frames_list');
const MAX_CANVAS_SIZE = 512;

const color = new Color();
const pencilTool = new Pencil();
const eraserTool = new Eraser();
const strokeTool = new Stroke();
const frameData = new FrameData(drawImage);
const localStorageUtility = new LocalStorageUtility(
  canvas, layer, currentColor, prev, MAX_CANVAS_SIZE,
);

localStorageUtility.getFromStorage().then(async (data) => {
  frameData.frameStack = data;
  const fragment = document.createDocumentFragment();
  data.forEach((item, index) => {
    const frame = new Frame(index, item.dataURL, drawImage);
    fragment.appendChild(frame.createFrame());
  });
  frameList.appendChild(fragment);
  const frame = document.querySelector('.frame_0');
  activateFrame(frame);
  const position = frameData.getActiveFrame();
  frameData.clearMainCanvas();
  frameData.changeCanvasIMG(frameData.frameStack[position].dataURL, canvas);
});

activateTool(pencil, 'cursor-pencil', bucket, grabColor);

document.querySelector('.color--input-red').addEventListener('click', (event) => {
  color.getDefaultColor(event);
  localStorageUtility.setColor();
});

document.querySelector('.color--input-blue').addEventListener('click', (event) => {
  color.getDefaultColor(event);
  localStorageUtility.setColor();
});

prev.addEventListener('click', (event) => {
  color.getPreviousColor(event);
  localStorageUtility.setColor();
});

currentColor.addEventListener('input', () => {
  color.changePrevColor();
  localStorageUtility.setColor();
});

currentColor.addEventListener('click', (event) => {
  color.setPrevColor(event.target.value);
  localStorageUtility.setColor();
});

bucket.addEventListener('click', () => {
  activateTool(bucket, 'cursor-bucket');
  strokeTool.disableLayer();
});

grabColor.addEventListener('click', () => {
  activateTool(grabColor, 'cursor-grab');
  strokeTool.disableLayer();
});

pencil.addEventListener('click', () => {
  activateTool(pencil, 'cursor-pencil');
  strokeTool.disableLayer();
});

eraser.addEventListener('click', () => {
  activateTool(eraser, 'cursor-eraser');
  strokeTool.activeLayer();
  strokeTool.disableLayer();
});

stroke.addEventListener('click', () => {
  activateTool(stroke, 'cursor-pencil');
  strokeTool.activeLayer();
});

canvas.addEventListener('click', (event) => {
  if (bucket.classList.contains('active')) {
    const fillBucket = new Fill(canvas);
    const point = { x: Math.round(event.offsetX), y: Math.round(event.offsetY) };
    const targetColor = fillBucket.getPixel(point);
    fillBucket.floodFill(point, targetColor, hexToRGBA(currentColor.value));
    fillBucket.fillColor();
  }
  if (grabColor.classList.contains('active')) {
    colorGrab(event);
    localStorageUtility.setColor();
  }
  frameData.changeFrameIMG(frameData.getActiveFrame(), canvas.toDataURL());
  localStorageUtility.setFrameStack(frameData.frameStack);
});

// drawing
canvas.addEventListener('mousedown', (event) => {
  if (pencil.classList.contains('active')) {
    pencilTool.setInitialCoordinate(event);
  }
  if (eraser.classList.contains('active')) {
    eraserTool.setInitialCoordinate(event);
  }
});

canvas.addEventListener('mousemove', (event) => {
  if (pencil.classList.contains('active')) {
    pencilTool.draw(event);
  }
  if (eraser.classList.contains('active')) {
    eraserTool.draw(event);
  }
});

canvas.addEventListener('mouseup', () => {
  if (pencil.classList.contains('active')) {
    pencilTool.doneDraw();
  }
  if (eraser.classList.contains('active')) {
    eraserTool.doneDraw();
  }
  localStorageUtility.setCanvasData();
});

canvas.addEventListener('mouseout', () => {
  if (pencil.classList.contains('active')) {
    pencilTool.doneDraw();
  }
  if (eraser.classList.contains('active')) {
    eraserTool.doneDraw();
  }
  localStorageUtility.setCanvasData();
});


layer.addEventListener('mousedown', (event) => {
  if (stroke.classList.contains('active')) {
    strokeTool.setInitialCoordinate(event);
  }
});


layer.addEventListener('mousemove', (event) => {
  if (stroke.classList.contains('active')) {
    strokeTool.draw(event);
  }
});

layer.addEventListener('mouseup', () => {
  if (stroke.classList.contains('active')) {
    strokeTool.doneDraw();
    frameData.changeFrameIMG(0, canvas.toDataURL());
  }
  localStorageUtility.setCanvasData();
});

layer.addEventListener('mouseout', () => {
  if (stroke.classList.contains('active')) {
    strokeTool.doneDraw();
    frameData.changeFrameIMG(0, canvas.toDataURL());
  }
  localStorageUtility.setCanvasData();
});

document.addEventListener('keydown', (event) => {
  switch (event.code) {
    case 'KeyB':
      activateTool(bucket, 'cursor-bucket', grabColor, pencil);
      break;
    case 'KeyC':
      activateTool(grabColor, 'cursor-grab', bucket, pencil);
      break;
    case 'KeyP':
      activateTool(pencil, 'cursor-pencil', bucket, grabColor);
      break;
    default:
  }
});

sizeSwitcher.addEventListener('click', (event) => {
  if (event.target.tagName === 'BUTTON') {
    changeCanvasSize(event.target);
    localStorageUtility.setCanvasSize();
  } else if (event.target.tagName === 'IMG') {
    changeCanvasSize(event.target.parentNode);
    localStorageUtility.setCanvasSize();
  }
});

frameList.addEventListener('mousedown', (event) => {
  let element;
  if (event.target.closest('.frames_list--item')) {
    element = event.target.closest('.frames_list--item');
    if (!element.classList.contains('active_frame')) {
      activateFrame(element);
      const position = frameData.getActiveFrame();
      frameData.clearMainCanvas();
      frameData.changeCanvasIMG(frameData.frameStack[position].dataURL, canvas);
    }
  }
  if (event.target.closest('.buttons--item')) {
    let position = frameData.getActiveFrame();
    element = event.target.closest('.buttons--item');
    const frameTools = new FrameTools(position, frameData.frameStack, activateFrame);
    switch (element.dataset.type) {
      case 'delete':
        frameTools.deleteFrame(frameData.getActiveFrame());
        position = frameData.getActiveFrame();
        frameData.clearMainCanvas();
        frameData.changeCanvasIMG(frameData.frameStack[position].dataURL, canvas);
        break;
      case 'copy':
        frameTools.duplicateFrame();
        break;
      default:
    }
  }
  const position = frameData.getActiveFrame();
  const frameTools = new FrameTools(position, frameData.frameStack, activateFrame);
  frameTools.addEvents();
  localStorageUtility.setFrameStack(frameData.frameStack);
});

addFrameButton.addEventListener('click', async () => {
  frameData.clearMainCanvas();
  const newFrame = new Frame(frameData.frameStack.length, canvas.toDataURL(), drawImage);
  frameList.appendChild(newFrame.createFrame());
  frameData.addFrameData({ dataURL: canvas.toDataURL() });
  const frame = document.querySelector(`.frame_${frameData.frameStack.length - 1}`);
  activateFrame(frame);
  localStorageUtility.setFrameStack(frameData.frameStack);
});
