export default function rgbToHex(color) {
  const rgbArrayLength = 2;
  let result = color.reduce((acc, item, index) => {
    if (index <= rgbArrayLength) {
      let itemColor = item.toString(16);
      if (itemColor.length === 1) {
        itemColor = `0${itemColor}`;
      }
      return acc + itemColor;
    }
    return acc;
  }, '');
  result = `#${result}`;
  return result;
}
