import drawImage from './drawImage';

export default class LocalStorageUtility {
  constructor(canvas, layer, currentColor, prevColor, MAX_CANVAS_SIZE) {
    this.canvas = canvas;
    this.layer = layer;
    this.currentColor = currentColor;
    this.prevColor = prevColor;
    this.MAX_CANVAS_SIZE = MAX_CANVAS_SIZE;
  }

  setCanvasData() {
    const dataURL = this.canvas.toDataURL();
    localStorage.setItem('dataURL', dataURL);
  }

  setFrameStack(frameStack) {
    this.frameStack = frameStack;
    localStorage.setItem('frameStack', JSON.stringify(this.frameStack));
  }

  setColor() {
    localStorage.setItem('currentColor', this.currentColor.value);
    localStorage.setItem('prevColor', this.prevColor.value);
  }

  setCanvasSize() {
    this.canvasSize = this.canvas.dataset.size;
    localStorage.setItem('canvasSize', this.canvasSize);
  }

  async getCanvasData() {
    const dataURL = localStorage.getItem('dataURL');
    if (dataURL) {
      await drawImage(this.canvas, dataURL);
    }
  }

  getFrameStack() {
    this.frameStack = JSON.parse(localStorage.getItem('frameStack'));
    if (this.frameStack) {
      return this.frameStack;
    }
    return [{
      dataURL: this.canvas.toDataURL(),
    }];
  }

  getColor() {
    this.currentColor.value = localStorage.getItem('currentColor');
    this.prevColor.value = localStorage.getItem('prevColor');
  }

  getCanvasSize() {
    if (localStorage.getItem('canvasSize')) {
      this.canvasSize = Number(localStorage.getItem('canvasSize'));
      this.canvas.dataset.size = this.canvasSize;
      this.layer.dataset.size = this.canvasSize;
      const size = Math.round(this.MAX_CANVAS_SIZE / Number(localStorage.getItem('canvasSize')));
      document.querySelector(`.size_switcher--button-${size}x${size}`).classList.add('active');
    } else {
      document.querySelector(`.size_switcher--button-${this.MAX_CANVAS_SIZE}x${this.MAX_CANVAS_SIZE}`).classList.add('active');
      this.canvasSize = 1;
      this.canvas.dataset.size = '1';
      this.layer.dataset.size = '1';
    }
    return this.canvasSize;
  }

  async getFromStorage() {
    await this.getCanvasData();
    this.getColor();
    this.getCanvasSize();
    return this.getFrameStack();
  }
}
