let dragSrcEl;
let currentEl;

export function dragStart(event) {
  dragSrcEl = event.target.closest('.frames_list--item');
  dragSrcEl.classList.add('drag_opacity');
}

export function dragEnter(event) {
  if (dragSrcEl !== event.target.closest('.frames_list--item')) {
    event.target.closest('.frames_list--item').classList.add('over');
    currentEl = event.target.closest('.frames_list--item');
  }
}

export function dragLeave(event) {
  event.stopPropagation();
  if (event.target.closest('.frames_list--item') !== currentEl) {
    event.target.closest('.frames_list--item').classList.remove('over');
  }
}

export function dragOver(event) {
  event.preventDefault();
  return false;
}

export function dragDrop(event) {
  event.preventDefault();
  if (dragSrcEl !== event.target.closest('.frames_list--item')) {
    return {
      currentElem: dragSrcEl.dataset.position,
      newPosition: event.target.closest('.frames_list--item').dataset.position,
    };
  }
  return false;
}

export function dragEnd() {
  const frames = document.querySelectorAll('.frames_list--item');
  Array.prototype.call(frames, (item) => {
    item.classList.remove('over');
  });
}
