import rgbToHex from './utility/rgbToHex';

function getColor(event) {
  const canvas = document.querySelector('.canvas');
  const ctx = canvas.getContext('2d');
  const color = ctx.getImageData(event.offsetX, event.offsetY, 1, 1).data;
  return rgbToHex(color);
}

export default function colorGrab(event) {
  const color = getColor(event);
  const currentColor = document.querySelector('.color--input-current');
  const prev = document.querySelector('.color--input-prev');
  if (currentColor.value !== color) {
    const prevValue = currentColor.value;
    currentColor.value = color;
    prev.value = prevValue;
  }
}
