import Pencil from './Pencil';

export default class Stroke extends Pencil {
  constructor() {
    super();
    this.tool = document.querySelector('.action--stroke');
    this.strokeCoordinate = [];
    this.layer = document.querySelector('.canvas_layer');
    this.MAX_CANVAS_SIZE = 512;
  }

  draw(event) {
    const pixelSize = this.getPixelSize();
    const finiteX = Math.floor(event.offsetX / pixelSize);
    const finiteY = Math.floor(event.offsetY / pixelSize);
    if (this.isDrawing && this.tool.classList.contains('active')) {
      this.clearLayer();
      this.clearStroke();
      this.drawAlgorithm(this.initialX, this.initialY, finiteX, finiteY, this.currentColor.value);
      this.strokeCoordinate.push(
        this.initialX,
        this.initialY,
        finiteX,
        finiteY,
        this.currentColor.value,
      );
    }
  }

  drawPix(x, y, color) {
    if (!this.isDrawing) {
      super.drawPix(x, y, color);
    } else {
      const pixelSize = this.getPixelSize();
      const ctx = this.layer.getContext('2d');
      ctx.fillStyle = color;
      ctx.fillRect(x * pixelSize, y * pixelSize, pixelSize, pixelSize);
    }
  }

  doneDraw() {
    this.isDrawing = false;
    this.clearLayer();
    this.drawAlgorithm(...this.strokeCoordinate);
  }

  clearStroke() {
    this.strokeCoordinate.splice(0, this.strokeCoordinate.length);
  }

  clearLayer() {
    const ctx = this.layer.getContext('2d');
    ctx.clearRect(0, 0, this.MAX_CANVAS_SIZE, this.MAX_CANVAS_SIZE);
  }

  disableLayer() {
    this.layer.classList.add('disable');
  }

  activeLayer() {
    this.layer.classList.remove('disable');
  }
}
