export default class Color {
  constructor() {
    this.currentColorInput = document.querySelector('.color--input-current');
    this.prevColorInput = document.querySelector('.color--input-prev');
  }

  getDefaultColor(event) {
    event.preventDefault();
    if (this.currentColorInput.value !== event.target.value) {
      this.prevColor = this.currentColorInput.value;
      this.currentColorInput.value = event.target.value;
      this.changePrevColor();
    }
  }

  getPreviousColor(event) {
    event.preventDefault();
    this.prevColor = this.currentColorInput.value;
    this.currentColorInput.value = this.prevColorInput.value;
    this.changePrevColor();
  }

  setPrevColor(value) {
    if (value) {
      this.prevColor = value;
    }
  }

  changePrevColor() {
    this.prevColorInput.value = this.prevColor;
  }
}
