export default class FrameData {
  constructor(drawImage) {
    this.frameStack = [];
    this.canvas = document.querySelector('.canvas');
    this.drawImage = drawImage;
  }

  addFrameData(data) {
    this.frameStack.push(data);
  }

  async changeFrameIMG(position, dataURL) {
    const canvasFrame = document.querySelector(`.canvas_frame_${position}`);
    this.frameStack[position].dataURL = dataURL;
    await this.changeCanvasIMG(dataURL, canvasFrame);
  }

  async changeCanvasIMG(dataURL, canvas) {
    canvas.getContext('2d').clearRect(0, 0, canvas.width, canvas.width);
    await this.drawImage(canvas, dataURL);
  }

  clearMainCanvas() {
    this.canvas.getContext('2d').clearRect(0, 0, this.canvas.width, this.canvas.height);
  }

  getActiveFrame() {
    this.frames = document.querySelectorAll('.frames_list--item');
    return Array.prototype.findIndex.call(this.frames, (item) => (item.classList.contains('active_frame')));
  }
}
