import Frame from './Frame';
import drawImage from '../tools/utility/drawImage';
import {
  dragDrop,
  dragEnd,
  dragEnter,
  dragLeave,
  dragOver,
  dragStart,
} from './FrameToolDragAndDrop';

export default class FrameTools {
  constructor(position, frameStack, activateFrame) {
    this.position = position;
    this.frame = document.querySelector(`.frame_${this.position}`);
    this.frameStack = frameStack;
    this.activateFrame = activateFrame;
  }

  addEvents() {
    const frameItems = document.querySelectorAll('.frames_list--item');
    Array.prototype.forEach.call(frameItems, (item) => {
      this.addEventsDragAndDrop(item, this.dragFrame);
    });
  }

  reRenderFrames() {
    const frameList = document.querySelector('.frames_list');
    const fragment = document.createDocumentFragment();
    this.frameStack.forEach((item, index) => {
      const frame = new Frame(index, item.dataURL, drawImage);
      fragment.appendChild(frame.createFrame());
    });
    frameList.innerHTML = '';
    frameList.appendChild(fragment);
    this.addEvents();
  }

  deleteFrame(activeFrame) {
    if (this.frameStack.length > 1) {
      const deletedFrame = this.frameStack.splice(this.position, 1);
      this.reRenderFrames();
      const frame = document.querySelector(`.frame_${activeFrame - 1}`);
      this.activateFrame(frame);
      return deletedFrame;
    }
    return false;
  }

  duplicateFrame() {
    this.frameStack.splice(this.position + 1, 0, this.frameStack[this.position]);
    this.reRenderFrames();
    const frame = document.querySelector(`.frame_${this.position + 1}`);
    this.activateFrame(frame);
  }

  dragFrame(position) {
    const { currentElem, newPosition } = position;
    const draggedFrame = this.frameStack.splice(currentElem, 1);
    this.frameStack.splice(newPosition, 0, ...draggedFrame);
    this.reRenderFrames();
    const frame = document.querySelector(`.frame_${newPosition}`);
    this.activateFrame(frame);
  }

  addEventsDragAndDrop(element) {
    element.addEventListener('dragstart', dragStart, false);
    element.addEventListener('dragenter', dragEnter, false);
    element.addEventListener('dragover', dragOver, false);
    element.addEventListener('dragleave', dragLeave, false);
    element.addEventListener('drop', (event) => {
      const dropElem = dragDrop(event);
      this.dragFrame(dropElem);
    }, false);
    element.addEventListener('dragend', dragEnd, false);
  }
}
