export default class Pencil {
  constructor() {
    this.isDrawing = false;
    this.initialX = null;
    this.initialY = null;
    this.currentColor = document.querySelector('.color--input-current');
    this.canvas = document.querySelector('.canvas');
    this.tool = document.querySelector('.action--pencil');
  }

  setInitialCoordinate(event) {
    const pixelSize = this.getPixelSize();
    this.isDrawing = true;
    this.initialX = Math.floor(event.offsetX / pixelSize);
    this.initialY = Math.floor(event.offsetY / pixelSize);
  }

  draw(event) {
    const pixelSize = this.getPixelSize();
    if (this.isDrawing && this.tool.classList.contains('active')) {
      this.drawAlgorithm(this.initialX, this.initialY, Math.floor(event.offsetX / pixelSize),
        Math.floor(event.offsetY / pixelSize), this.currentColor.value);
      this.initialX = Math.floor(event.offsetX / pixelSize);
      this.initialY = Math.floor(event.offsetY / pixelSize);
    }
  }

  doneDraw() {
    this.isDrawing = false;
  }

  drawPix(x, y, color) {
    const pixelSize = this.getPixelSize();
    const ctx = this.canvas.getContext('2d');
    ctx.fillStyle = color;
    ctx.fillRect(x * pixelSize, y * pixelSize, pixelSize, pixelSize);
  }

  drawAlgorithm(initX, initY, finiteX, finiteY, color) {
    const deltaX = Math.abs(finiteX - initX);
    const deltaY = Math.abs(finiteY - initY);
    const signX = (initX < finiteX) ? 1 : -1;
    const signY = (initY < finiteY) ? 1 : -1;
    let error = deltaX - deltaY;
    let x = initX;
    let y = initY;
    let isEmpty = true;
    while (isEmpty) {
      this.drawPix(x, y, color);
      const secondError = 2 * error;
      if ((x === finiteX) && (y === finiteY)) {
        isEmpty = false;
      } else {
        if (secondError > -deltaY) {
          error -= deltaY;
          x += signX;
        }
        if (secondError < deltaX) {
          error += deltaX;
          y += signY;
        }
      }
    }
  }

  getPixelSize() {
    const canvasSize = this.canvas.dataset.size;
    const pencilSize = document.querySelector('.input--radio:checked').value;
    return canvasSize * pencilSize;
  }
}
