export default class Fill {
  constructor(canvas) {
    this.ctx = canvas.getContext('2d');
    this.imageData = this.ctx.getImageData(0, 0, canvas.width, canvas.height);
    this.fillStack = [];
    this.AMOUNT_OF_COLORS = 4;
  }

  floodFill(point, targetColor, fillColor) {
    if (!Fill.colorMatching(targetColor, fillColor)) {
      const currentColor = this.getPixel(point);
      if (Fill.colorMatching(currentColor, targetColor)) {
        this.setPixel(point, fillColor);
        this.fillStack.push([{ x: point.x + 1, y: point.y }, targetColor, fillColor]);
        this.fillStack.push([{ x: point.x - 1, y: point.y }, targetColor, fillColor]);
        this.fillStack.push([{ x: point.x, y: point.y + 1 }, targetColor, fillColor]);
        this.fillStack.push([{ x: point.x, y: point.y - 1 }, targetColor, fillColor]);
      }
    }
  }

  fillColor() {
    const range = this.fillStack.length;
    if (this.fillStack.length) {
      this.fillStack.forEach((item) => {
        this.floodFill(...item);
      });
      this.clearFillStack(range);
      this.fillColor();
    } else {
      this.ctx.putImageData(this.imageData, 0, 0);
      this.clearFillStack(this.fillStack.length);
    }
  }

  static colorMatching(firstColor, secondColor) {
    return firstColor.join(',') === secondColor.join(',');
  }

  clearFillStack(range) {
    this.fillStack.splice(0, range);
  }

  setPixel(point, fillColor) {
    const { data, width } = this.imageData;
    const offset = (point.y * width + point.x) * this.AMOUNT_OF_COLORS;
    [data[offset], data[offset + 1], data[offset + 2], data[offset + 3]] = fillColor;
  }

  getPixel(point) {
    const offset = (point.y * this.imageData.width + point.x) * this.AMOUNT_OF_COLORS;
    return [
      this.imageData.data[offset],
      this.imageData.data[offset + 1],
      this.imageData.data[offset + 2],
      this.imageData.data[offset + 3],
    ];
  }
}
