export default class Frame {
  constructor(position, dataURL, drawImage) {
    this.position = position;
    this.dataURL = dataURL;
    this.drawImage = drawImage;
  }

  createFrame() {
    const container = document.createElement('li');
    const canvasFrame = document.createElement('canvas');
    canvasFrame.width = 128;
    canvasFrame.height = 128;
    canvasFrame.classList.add('canvas_frame', `canvas_frame_${this.position}`);
    canvasFrame.dataset.position = this.position;
    this.addData(canvasFrame);
    container.classList.add('frames_list--item', `frame_${this.position}`);
    container.dataset.position = this.position;
    container.draggable = 'true';
    container.innerHTML = `
        <div class="frames_list--buttons">
          <div class="buttons--item buttons--item-text">${this.position + 1}</div>
          <button class="buttons--item" title="Delete this frame" data-type="delete"><i class="fas fa-trash-alt"></i></button>
          <div class="buttons--item" data-type="drag"><i class="fas fa-arrows-alt buttons--item-icon"></i></div>
          <button class="buttons--item" title="Duplicate this frame" data-type="copy"><i class="fas fa-copy"></i></button>
        </div>
    `;
    container.appendChild(canvasFrame);
    return container;
  }

  async addData(canvasFrame) {
    await this.drawImage(canvasFrame, this.dataURL);
  }
}
